import { Injectable } from '@angular/core';
import { CONFIG } from 'src/config';
import { MetamaskService } from './metamask.service';
import Web3 from "web3";
import { AppService } from './app.service';

@Injectable({
  providedIn: 'root'
})
export class ProductService {

  CONFIG = CONFIG
  ABI = JSON.parse(JSON.stringify(CONFIG.ABI))

  constructor(private metamaskService: MetamaskService, private appService: AppService) { }

  async getCollectionDetail(item: any) {
    try {
      const provider = await this.metamaskService.getProvider()
      const web3 = new Web3(provider);
      const contract = await new web3.eth.Contract(this.ABI, item.contractAddress);
      item.maxSupply = await contract.methods.maxSupply().call();
      item.price = await contract.methods.getPrice().call();
      item.sold = await contract.methods.totalSupply().call();


    } catch (error) {

    }
  }

  async getMaxSupply(contractAddress: string) {
    try {
      const provider = await this.metamaskService.getProvider()
      const web3 = new Web3(provider);
      const contract = await new web3.eth.Contract(this.ABI, contractAddress);
      const price = await contract.methods.maxSupply().call();
      console.log(price);
      return Number(price); /// Math.pow(10, 18);
    } catch (error) {
      return 0;
    }
  }

  async getTotalSupply(contractAddress: string) {
    try {
      const provider = await this.metamaskService.getProvider()
      const web3 = new Web3(provider);
      const contract = await new web3.eth.Contract(this.ABI, contractAddress);
      const price = await contract.methods.totalSupply().call();
      //console.log(price);
      return Number(price); /// Math.pow(10, 18);
    } catch (error) {
      return 0;
    }
  }

  async getPrice(contractAddress: string) {
    try {
      const provider = await this.metamaskService.getProvider()
      const web3 = new Web3(provider);
      const contract = await new web3.eth.Contract(this.ABI, contractAddress);
      const price = await contract.methods.getPrice().call();
      //console.log(price);
      return Number(price); /// Math.pow(10, 18);
    } catch (error) {
      return 0;
    }
  }

  async buy(contractAddress: any, from: any, price: any, num: any) {
    try {
      //const account = localStorage.getItem("user_address");
      const account = this.appService.account.address;
      if (account) {
        const provider = await this.metamaskService.getProvider()
        const web3 = new Web3(provider);
        const contract = await new web3.eth.Contract(this.ABI, contractAddress);
        // console.log(price, num);
        contract.methods
          .buy(num)
          .send({
            from: from,
            value: price,
            gas: await contract.methods
              .buy(num)
              .estimateGas({ from: from, value: price }),
            gasPrice: await web3.eth.getGasPrice(),
          }).on("transactionHash", async function (transactionHash: any) {
            //setDisableMint(true);

            let link;

            if (CONFIG.NETWORK_ID === 1) {
              link = "https://etherscan.io/tx/" + transactionHash;
            } else if (CONFIG.NETWORK_ID === 4) {
              link = "https://rinkeby.etherscan.io/tx/" + transactionHash;
            }

            return link;
            let count = 0;
            let timer = setInterval(async function () {
              count = count + 1;
              web3.eth.getTransactionReceipt(
                transactionHash,
                async function (err, data) {
                  if (data !== null || count > 30) {
                    clearInterval(timer);
                  }
                  // console.error(err, data);
                }
              );
            }, 1000);


          });
      } else {
        this.metamaskService.onConnect()
      }
    } catch (error) {
      // console.error(error);
    }
  };

}
