import { Injectable, NgZone } from '@angular/core';
import Web3 from "web3";
import Onboard from "bnc-onboard";
import { CONFIG } from 'src/config';
import { AppService } from './app.service';
@Injectable({
  providedIn: 'root'
})
export class MetamaskService {
  networkId = Number(CONFIG.NETWORK_ID);
  projectId = CONFIG.PROJECT_ID;
  onboardApiKey = CONFIG.ONBOARD_API_KEY;
  wallets = [{ walletName: "metamask", preferred: true }];
  web3: any
  provider: any;

  onboard = Onboard({
    dappId: this.onboardApiKey, // [String] The API key created by step one above
    networkId: this.networkId, // [Integer] The Ethereum network ID your Dapp uses.
    darkMode: true,
    subscriptions: {
      wallet: (wallet) => {
        console.log(wallet)
        this.provider = wallet.provider;
      },
      address: () => {
        //console.log(wallet)
        const oldAccount = localStorage.getItem("user_address");
        if (oldAccount) {
          //this.onDisconnect();
        }
      },
      network: () => {
        const oldAccount = localStorage.getItem("user_address");
        if (oldAccount) {
          //this.onDisconnect();
        }
      },
    },
    walletSelect: {
      wallets: this.wallets,
    },
  })



  constructor(private appService: AppService, private zone: NgZone) { }


  async balance() {
    let balance = 0;
    try {
      const account = localStorage.getItem("user_address");
      if (account) {
        //this.getProvider();
        const provider = await this.getProvider()
        const web3 = new Web3(provider);
        let accBalance = await web3.eth.getBalance(account);
        balance = Number(accBalance)
      }
    } catch (error) {
      // console.error(error);
    }

    return balance / Math.pow(10, 18);
  };

  async onConnect() {
    try {
      const walletSelected = await this.onboard.walletSelect();
      const readyToTransact = await this.onboard.walletCheck();

      if (walletSelected && readyToTransact) {
        const currentState = await this.onboard.getState();
        //console.log(currentState);
        await this.setAccount(currentState.address);
      }
    } catch (error) {
      // console.error(error);
    }
  };

  async onDisconnect() {
    await this.onboard.walletReset();
    await this.clearAccount();
  };

  async setAccount(account: any) {
    localStorage.setItem("user_address", account);
    this.appService.account.address = account;
    this.appService.account.balance = await this.balance();
    console.log(this.appService.account)
  };

  async clearAccount() {
    localStorage.clear();
    this.appService.account.address = "";
    this.appService.account.balance = 0;
  };

  async getProvider() {

    let result;
    if (!this.provider) {
      if (this.networkId === 1) {
        //new Web3.providers.HttpProvider()
        result = new Web3.providers.HttpProvider("https://mainnet.infura.io/v3/" + this.projectId);
      } else if (this.networkId === 4) {
        result = new Web3.providers.HttpProvider("https://rinkeby.infura.io/v3/" + this.projectId);
      }
      this.zone.run(() => {

      })
    } else {
      result = this.provider;
    }
    return result;
  };

}
