import { AfterViewInit, Component, ElementRef, OnInit, ViewChild } from '@angular/core';

@Component({
  selector: 'app-part2',
  templateUrl: './part2.component.html',
  styleUrls: ['./part2.component.scss']
})
export class Part2Component implements OnInit,AfterViewInit {

  @ViewChild('box') box: ElementRef | undefined;
  @ViewChild('image') img: any | undefined;
  constructor() { }

  ngAfterViewInit(): void {
    this.calSize();
  }

  ngOnInit(): void {
  }
  onResize($event: any) {
    this.calSize();
  }

  calSize() {
    const boxHeight = this.box?.nativeElement.offsetHeight;
    this.img.nativeElement.style.height = `${boxHeight}px`;
  }
}
