import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { CONFIG } from 'src/config';

@Injectable({
  providedIn: 'root'
})
export class CollectionService {

  CONFIG = CONFIG
  constructor(private http: HttpClient) { }

  summary() {
    return this.http.post(CONFIG.BASE_API + "/collection/summary", {})
  }
  getByName() {
    return this.http.post(CONFIG.BASE_API + "/collection/get-by-name", {})
  }
  getAll() {
    return this.http.post(CONFIG.BASE_API + "/collection/get-all", {})
  }
}
