import { AfterViewInit, Component, ElementRef, OnInit, ViewChild } from '@angular/core';

@Component({
  selector: 'app-part5',
  templateUrl: './part5.component.html',
  styleUrls: ['./part5.component.scss']
})
export class Part5Component implements OnInit, AfterViewInit {

  @ViewChild('right') rightLeg: ElementRef | undefined;
  @ViewChild('left') leftLeg: ElementRef | undefined;
  @ViewChild('middle') middle: any | undefined;
  constructor() { }
  ngAfterViewInit(): void {

    this.calSize();
  }

  onResize($event: any) {
    this.calSize();
  }

  calSize() {
    const x1 = this.rightLeg?.nativeElement.getBoundingClientRect().x;
    console.log(this.rightLeg?.nativeElement.getBoundingClientRect())
    const x2 = this.leftLeg?.nativeElement.getBoundingClientRect().x;
    const range = x1 - x2 + 4;

    this.middle.nativeElement.style.width = `${range}px`;
  }
  ngOnInit(): void {
    // const { x1, y1 } = this.rightLeg?.nativeElement.getBoundingClientRect();
    // const { x2, y2 } = this.leftLeg?.nativeElement.getBoundingClientRect();
  }

}
