import { Component, OnInit } from '@angular/core';
import { AppService } from './services/app.service';
import { CollectionService } from './services/collection.service';
import { MetamaskService } from './services/metamask.service';
import { ProductService } from './services/product.service';
import * as Aos from 'aos';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  isReady = false;
  constructor(
    private metamaskService: MetamaskService,
    public appService: AppService,
    private collectionService: CollectionService,
    private productService: ProductService) {

  }
  ngOnInit(): void {
    Aos.init()
  }
  onBuy(contractAddress: any, from: any, price: any, num: any) {
    this.productService.buy(contractAddress, from, price, num)
  }

  onConnect() {
    this.metamaskService.onConnect();
    this.collectionService.getAll().subscribe(async (collection: any) => {
      for (let item in collection.data) {
        if (collection.data.hasOwnProperty(item)) {
          collection.data[item].name = item;
          await this.productService.getCollectionDetail(collection.data[item])
          this.appService.collections.push(collection.data[item]);
        }
      }
    });


  }
  title = 'nft-market';
}
